.color {
  position: absolute;
  width: 400px;
  height: 300px;
  background: var(--primary-color);
  filter: blur(500px);
  transform: rotate(-8.96deg);
  animation-duration: 10s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

#color-1 {
  left: -200px;
  top: 107.81px;
  opacity: 0.3;
  animation-name: color-1;
}
#color-3 {
  right: 0;
  top: 400px;
  opacity: 0.3;
}
#color-2 {
  right: 0;
  bottom: 0;
  opacity: 0.3;
  animation-name: color-2;
}

.hero .content h1 {
  animation-name: h1;
  animation-duration: 1.5s;
  animation-direction: alternate;
}

.hero .content h2 {
  animation-name: h2;
  animation-duration: 1.5s;
  animation-delay: 0.1s;
}

.hero .content span {
  animation: arrow alternate 4s;
}
