@keyframes color-1 {
  0% {
    filter: blur(400px);
    left: 0;
    left: -200px;
  }
  50% {
    filter: blur(600px);
    height: 400px;
    left: 25px;
  }
  100% {
    filter: blur(1000px);
    height: 600px;
    left: 50px;
  }
}
@keyframes color-2 {
  0% {
    filter: blur(400px);
    bottom: 0;
    height: 300px;
  }
  50% {
    filter: blur(600px);
    height: 400px;
    bottom: -50px;
  }
  100% {
    filter: blur(1000px);
    height: 600px;
    bottom: -100px;
  }
}

@keyframes h1 {
  from {
    transform: translateY(100px);
    -webkit-text-stroke: 3px white;
  }
  to {
    transform: translateY(0px);
    -webkit-text-stroke: none;
    color: white;
  }
}
@keyframes h2 {
  from {
    transform: translateY(100px);
  }
  to {
    transform: translateY(0px);
  }
}
@keyframes arrow {
  from {
    transform: translateY(50px);
  }
  to {
    transform: translateY(-50px);
  }
}
